import * as React from 'react';import PropTypes from "prop-types";import Typography from '@mui/material/Typography';import Grid from '@mui/material/Grid';import Card from '@mui/material/Card';
import CardActionArea from '@mui/material/CardActionArea';import CardContent from '@mui/material/CardContent';import CardMedia from '@mui/material/CardMedia';
import MuiLink from '@mui/material/Link';
//       onClick=     {event => (event.stopPropagation(); event.preventDefault() )}               console.log("CardActionArea clicked");
function FeaturedPost(props) {const { post, szlink } = props;  return (
    <Grid item xs={12} md={6}>
      <CardActionArea component="a" href={szlink} 
      onMouseDown= {event => event.stopPropagation()} 
      onClick={event => {
              event.stopPropagation();
              event.preventDefault();
              //console.log("Button clicked="+szlink);
              window.open(szlink, '_blank'); // https://www.google.co.uk/maps/@51.5954613,-0.083975,3a,75y,320.86h,94.47t/data=!3m6!1e1!3m4!1sQPJPFXWlg9bzIKPu1jWOUQ!2e0!7i16384!8i8192!5m1!1e4
              // window.location.href={szlink}; lh https://www.google.co.uk/maps/@51.5941112,-0.0872921,63a,35y,180h,39.59t/data=!3m1!1e3!5m1!1e4
            }}
      onTouchStart={event => event.stopPropagation()} 
      
      >    
        <Card sx={{ display: 'flex' }}>
          <CardContent sx={{ flex: 1 }}>
            <Typography component="h2" variant="h5">                {post.title}        </Typography>
            <Typography variant="subtitle1" color="text.secondary"> {post.date}         </Typography>
            <Typography variant="subtitle1" paragraph>              {post.description}  </Typography>
            <Typography variant="subtitle1" color="primary">        {post.lkntit}       </Typography>
          </CardContent>
          <CardMedia component="img" sx={{ width: 160, display: { xs: 'none', sm: 'block' } }} image={post.image} alt={post.imageLabel} />
        </Card>
      </CardActionArea>
    </Grid>
  );
}

FeaturedPost.propTypes = {
  post: PropTypes.shape({    date: PropTypes.string.isRequired,    description: PropTypes.string.isRequired,    image: PropTypes.string.isRequired,    imageLabel: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,  }).isRequired,}; export default FeaturedPost;
