import * as React from 'react';import PropTypes from "prop-types";import Grid from '@mui/material/Grid';import Stack from '@mui/material/Stack';import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';import Link from '@mui/material/Link';
import TwitterIcon from '@mui/icons-material/Twitter'; import FacebookIcon from '@mui/icons-material/Facebook';

function Sidebar(props) { const { archives, description, szsocicons, title } = props;  return (
    <Grid item xs={12} md={4}>
      <Paper elevation={0} sx={{ p: 2, bgcolor: 'grey.200' }}>
        <Typography variant="h6" gutterBottom>{title}</Typography>
        <Typography>{description}</Typography></Paper>
      <Typography variant="h6" gutterBottom sx={{mt:3 }}>Archives</Typography>{archives.map((archive) => (<Link display="block" variant="body1" href={archive.url} key={archive.title}>{archive.title}</Link>))}
      <Typography variant="h6" gutterBottom sx={{mt:3 }}>Social</Typography>
      {szsocicons.map((network) => (<Link display="block" variant="body1" href="#" key={network.name} sx={{mb:0.5 }} >
               <Stack direction="row" spacing={1} alignItems="center">
                   {network.icon ==='TwitterIcon' ? <TwitterIcon /> : <FacebookIcon />}
               <span>{network.name}</span></Stack></Link>))}
    </Grid>  );}

Sidebar.propTypes = {archives: PropTypes.arrayOf(PropTypes.shape({ title: PropTypes.string.isRequired,  url: PropTypes.string.isRequired,  }), ).isRequired,
  description: PropTypes.string.isRequired,
  szsocicons: PropTypes.arrayOf( PropTypes.shape({icon: PropTypes.elementType.isRequired, name: PropTypes.string.isRequired, }), ).isRequired,
  title: PropTypes.string.isRequired,}; export default Sidebar;
