const IndexPage = ({data, location}) => {
  const szdata = useStaticQuery(graphql`{site(siteMetadata: {}) { id siteMetadata {title } } } `);
  
  
  // console.log("31 szdata="+JSON.stringify(szdata.site.siteMetadata.title))
  const szSiteTitle=szdata.site.siteMetadata.title;
  const { settings } = useSettings();// console.log("33 settings="+JSON.stringify(settings))
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  let theme = useMemo(  () =>  createTheme({ palette: {  mode: prefersDarkMode ? 'dark' : 'light',  },    }),  [prefersDarkMode],  );
  const [darkMode, setDarkMode] = useState('light');  
  const darkModeHandler = () => {setDarkMode(darkMode === 'light' ? 'dark' : 'light');  };
  return (<ThemeProvider theme={theme}>      <CssBaseline />
    <Container maxWidth="lg">
      <SzHeader sz2title={szSiteTitle} szsubmenu={SzG.submenu} />
      <main><MainFeaturedPost post={szmaincontent} />
        <Grid container spacing={4}>{szcontent.map((post) => (<FeaturedPost key={post.title} post={post} szlink={post.lnk} szlkntit={post.lnktit} /> ))}</Grid>
        {/*<Grid container spacing={5} sx={{ mt: 3 }}>
             <Main title="From the firehose" posts={posts} /> 
              <Sidebar title={SzG.sidebar.title} description={SzG.sidebar.description} archives={SzG.sidebar.archives} szsocicons={SzG.sidebar.social} /> </Grid>*/}
      </main>

    </Container>
    <SzFooter title="" description="" /></ThemeProvider>);}
export default IndexPage;
