import * as React from 'react';import PropTypes from "prop-types";import Paper from '@mui/material/Paper';import Typography from '@mui/material/Typography';import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';import Box from '@mui/material/Box';

function MainFeaturedPost(props) { const { post } = props;

  return (
    <Paper sx={{ position: 'relative', backgroundColor: 'grey.800', color: '#fff', mb: 4, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundImage: `url(${post.image})`,
      }}
    >
      {/* Increase the priority of the hero background image */}
      {<img style={{ display: 'none' }} src={post.image} alt={post.imageText} />}
      <Box sx={{   position: 'absolute',   top: 0,   bottom: 0,   right: 0,   left: 0,   backgroundColor: 'rgba(0,0,0,.3)', }}
      />
      <Grid container>
        <Grid item md={6}>
          <Box sx={{position: 'relative', p: { xs: 3, md: 6 }, pr: { md: 0 }, }}   >
            <Box sx={{ boxShadow: 2, width: '32rem',  height: '5rem',  bgcolor: (theme) => (theme.palette.mode === 'dark' ? '#101010' : '#fff'),  color: (theme) =>
                theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800',  p: 1,   m: 1,  borderRadius: 2,  textAlign: 'center',  fontSize: '0.875rem', fontWeight: '700',  }} >
                 <Typography component="h1" variant="h3" color="inherit" gutterBottom >{post.title}</Typography>
            </Box>
                        <Box sx={{ boxShadow: 2, width: '38rem',  height: '3rem',  bgcolor: (theme) => (theme.palette.mode === 'dark' ? '#101010' : '#fff'),  color: (theme) =>
                theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800',  p: 1,   m: 1,  borderRadius: 2,  textAlign: 'center',  fontSize: '0.875rem', fontWeight: '700',  }} >
                 <Typography variant="h5" color="inherit" paragraph >
                 {post.description}</Typography>
            </Box>
            {/* <Link variant="subtitle1" href="#">{post.linkText}</Link> */}
          </Box>
        </Grid>
      </Grid>
    </Paper>  );}

MainFeaturedPost.propTypes = {
  post: PropTypes.shape({    description: PropTypes.string.isRequired,    image: PropTypes.string.isRequired,    imageText: PropTypes.string.isRequired,    linkText: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,  }).isRequired,
};

export default MainFeaturedPost; // sx={{ textShadow: '-1px', '-1px', 0, '#000',  '1px', '-1px', 0, '#000',  '-1px', '1px', 0, '#000', '1px', '1px', 0, '#000' }}
