import React, {  createContext,  useState} from 'react'; import {Link } from "gatsby"; import { colors } from '@mui/material';

const sztypography= {fontFamily: 
      [ '-apple-system', 'BlinkMacSystemFont', '"Segoe UI"', 'Roboto', '"Helvetica Neue"', 'Arial',  'sans-serif', '"Apple Color Emoji"', '"Segoe UI Emoji"', '"Segoe UI Symbol"',  ].join(','),
fontSize: 13,  fontWeightLight: 300,  fontWeightRegular: 400,  fontWeightMedium: 500,  fontWeightBold: 700,
      h1: {fontSize: "5rem", fontWeight: 700, lineHeight: 1.25, }, h2: {fontSize: "3.75rem", fontWeight: 700, lineHeight: 1.25, },  h3: {fontSize: "2.125rem", fontWeight: 700, lineHeight: 1.25, },
      h4: {fontSize: "1.3125rem", fontWeight: 500, lineHeight: 1.25, },  h5: {fontSize: "1.0625rem", fontWeight: 500, lineHeight: 1.25, },  h6: {fontSize: "1rem", fontWeight: 500, lineHeight: 1.25, },
      body1: {fontSize: 13, },  button: {textTransform: "none", },
};

const szLightPal={palette: { type: 'light', primary: {main: '#556cd6', }, secondary: {light: '#0066ff', main: '#19857b', }, error: { main: colors.red.A400, }, background: {default: '#fff', },  }, };
const szDarkPal ={palette: { type: 'dark',  primary: {main: '#90caf9', }, secondary: {light: '#0066ff', main: '#f48fb1', }, error: { main: colors.red.A400, }, background: {default: '#212121',   paper: '#424242', },  },}; 
  
const sztheme = { 'light': {sztypography, szLightPal}, 'dark':  {sztypography, szLightPal} ,};
// console.log("17 data="+JSON.stringify(data))
const defaultSettings = {theme: sztheme.light, };  // you could add sort of global settings here
const SettingsContext = createContext({ settings: defaultSettings,  saveSettings: () => {} });

export const SettingsProvider = ({ settings, children }) => {
  const [currentSettings, setCurrentSettings] = useState(settings || defaultSettings);return (<SettingsContext.Provider value={{settings: currentSettings, }}>{children}</SettingsContext.Provider> );};
export const szthemes=sztheme; 
export const SettingsConsumer = SettingsContext.Consumer; 
export default SettingsContext; 
